@mixin active-button {
  border: 1px solid transparent;
  background: #dde3ee;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  color: #232323;
  scale: 1.1;
}

@mixin common-font {
  font-style: normal;
  font-size: 18px;
  line-height: 21px;
}

.avia-list-item {
  background: #ffffff;
  margin: 71px auto;
  width: 980px;
  box-shadow: 0px 0px 14px rgba(112, 121, 153, 0.3);
  border-radius: 15px;
  display: flex;
}

.avia-list-company {
  display: flex;
  flex-direction: column;
  align-items: center;

  p {
    @include common-font;
    margin-top: 8px;
    margin-bottom: 41px;
    font-weight: 400;
  }
}
.avia-type {
  border-radius: 15px 0px;
  background: #8ba5d8;
  color: white;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 14px;
  padding: 6px 24px;
  margin-bottom: 30px;
}
.avia-main-part {
  flex: 1;
  display: flex;
  flex-direction: column;
}
.avia-list-duration {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex: 1;
  margin-top: 20px;
  img {
    margin-right: 30px;
  }
}
.avia-options-from {
  margin-right: 31px;
  padding-left: 29px;
  flex: 1;
}
.duration-scheme {
  margin-right: 28px;
  margin-bottom: 30px;
}
.duration {
  text-align: center;
  font-style: normal;
  font-size: 14px;
  line-height: 20px;
  line-height: 140%;
  color: #b7bac1;
}
.avia-options-to {
  margin-right: 33px;
  flex: 1;
}
.time {
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  color: #232323;
}
.city {
  margin-top: 8px;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 16px;

  color: #5c5c5c;
}
.date {
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;

  color: #5c5c5c;
}
.avia-time-options {
  display: flex;
  margin-bottom: 27px;
  margin-left: 30px;
}
.avia-time-button {
  @include common-font;
  padding: 10px 18px 9px 15px;
  font-weight: 500;
  color: #5c5c5c;
  background: #ffffff;
  border: 1px solid #b7bac1;
  border-radius: 10px;
  cursor: pointer;
  span {
    font-size: 22px;
  }

  &:not(:last-child) {
    margin-right: 20px;
  }

  &:hover {
    transition: all 0.1s ease-in-out;
    @include active-button;
  }
}
.active {
  transition: all 0.1s ease-in-out;
  @include active-button;
}

.avia-price {
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 38px;
  color: #232323;
  display: flex;
  align-items: center;
  justify-content: center;
  border-left: 1px solid #dde3ee;
  padding-left: 30px;
  padding-right: 29px;
}
